const getArticleList = () => {
  var fetchAction =  require('fetch');
  var url = "https://data.h34-imago26-stg.hasura-app.io/v1/query";
  var requestOptions = {
      "method": "POST",
      "headers": {
          "Content-Type": "application/json"
      }
  };
  var body = {
      "type": "select",
      "args": {
          "table": "article",
          "columns": [
              "body",
              "id",
              "author"
          ]
      }
  };
  requestOptions["body"] = JSON.stringify(body);
  fetchAction(url, requestOptions)
  .then(function(response) {
    return response.json();
  })
  .then(function(result) {
    console.log(result);
  })
  .catch(function(error) {
    console.log('Request Failed:' + error);
  });
};

const getArticle = (id) => {
  var fetchAction =  require('fetch');

  var url = "https://data.h34-imago26-stg.hasura-app.io/v1/query";

  var requestOptions = {
      "method": "POST",
      "headers": {
          "Content-Type": "application/json"
      }
  };

  var body = {
      "type": "select",
      "args": {
          "table": "article",
          "columns": [
              "body",
              "id",
              "author"
          ],
          "where": {
              "id": {
                  "$eq": id
              }
          }
      }
  };

  requestOptions["body"] = JSON.stringify(body);

  fetchAction(url, requestOptions)
  .then(function(response) {
    return response.json();
  })
  .then(function(result) {
    console.log(result);
  })
  .catch(function(error) {
    console.log('Request Failed:' + error);
  });
};

export {
  getArticle, getArticleList
}


